<template>
<div class="slide slide-slider" :class="conf.class ? conf.class : ''">

    <h1>{{ conf.header }}</h1>
    <el-carousel :height="conf.height">
        <el-carousel-item v-for="item in conf.items" :key="item">
        <div :class="'item'+item" v-html="item" />
        </el-carousel-item>
    </el-carousel>
</div>
</template>

<script>
export default {
    props: {
        conf: Object,
    },
};
</script>



