<template>
<div class="slide slide-boolean" :class="conf.class ? conf.class : ''" ref="container">
    <h1>{{ conf.header }}</h1>
    <div class="question">{{ conf.question }}</div>
    <div class="answers">
        <button @click="checkAnswer(1)" :class="previousAnswer.answer === 1 ? 'selected' : ''">
            True
        </button>
        <button @click="checkAnswer(0)" :class="previousAnswer.answer === 0 ? 'selected' : ''">
            False
        </button>
    </div>
</div>
</template>

<script>
export default {
    props: {
        conf: Object,
        saveAnswer: Function,
        previousAnswer: Object,
        popup: Function,
    },
    methods: {
        checkAnswer(value) {
            if ((value ? true : false) === this.conf.answer) {
                this.saveAnswer({
                    answer: value,
                    isCorrect: true,
                });
                this.popup(true, this.conf.correctMessage);
            } else {
                this.saveAnswer({
                    answer: value,
                    isCorrect: false,
                });
                this.popup(false, this.conf.incorrectMessage);
            }
        },
    },
};
</script>



