<template>
<div class="slide slide-multi" :class="conf.class ? conf.class : ''" ref="container">

    <h1>{{ conf.header }}</h1>
    <div class="question">{{ conf.question }}</div>
    <ul class="answers">
        <li :key="index" v-for="(answer, index) in conf.answers">
            <input type="checkbox" name="quiz" :id="'answer'+index" :checked="isChecked(index)" @change="checkAnswer(index, $event)" />
            <label :for="'answer'+index" v-html="answer" />
        </li>
    </ul>
</div>
</template>

<script>
export default {
    props: {
        conf: Object,
        saveAnswer: Function,
        previousAnswer: Object,
        popup: Function,
    },
    data() {
        return {
            answer: []
        };
    },
    methods: {
        checkAnswer(value, e) {
            const ind = this.answer.indexOf(value);
            if (e.currentTarget.checked === true && ind < 0) {
                this.answer.push(value);
            } else if (e.currentTarget.checked === false && ind >= 0) {
                this.answer.splice(ind, 1);
            }

            const correct = this.compareArrays(this.answer, this.conf.answer);
            this.saveAnswer({
                answer: this.answer,
                isCorrect: correct,
            }, correct);
            if (correct) {
                this.popup(correct, this.conf.correctMessage);
            }
        },
        compareArrays(a, b) {
            if (a === b) return true;
            if (a == null || b == null) return false;
            if (a.length != b.length) return false;

            a.sort();
            b.sort();

            for (let i = 0; i < a.length; ++i) {
                if (a[i] !== b[i]) return false;
            }
            return true;
        },
        isChecked(i) {
            return this.answer.indexOf(i) >= 0 ? true : false;
        },
    },
};
</script>



