import introContent from './slides/intro.html';
import outroContent from './slides/outro.html';

export default {
    slides: [
        {
            type: 'intro',
            header: 'Intro Slide',
            content: introContent,
        },
        {
            type: 'boolean',
            header: 'Boolean Question Slide',
            question: 'Earth is in shape of a perfect sphere.',
            answer: false,
            incorrectMessage: 'Wrong. Try Again',
            correctMessage: 'That\'s right!',
        },
        {
            type: 'quiz',
            header: 'Quiz Slide',
            question: 'What proportion of Earth does water cover?',
            answers: [
                'A half',
                'Three <strong>quarters</strong>',
                'A quarter',
            ],
            answer: 1,
            incorrectMessage: 'Wrong. Try Again',
            correctMessage: 'That\'s right!',
        },
        {
            type: 'drag-n-drop',
            header: 'Drag & Drop Slide',
        },
        {
            type: 'multi',
            class: 'custom-multi-class',
            header: 'Quiz with multiple correct answers',
            question: 'Which one is correct?',
            answers: [
                '1+1=1',
                '1*1=1',
                '1!=1',
            ],
            answer: [1, 2],
            incorrectMessage: 'Wrong. Try Again',
            correctMessage: 'That\'s right!',
        },
        {
            type: 'slider',
            header: 'Slider/Carouser example',
            items: [
                'sth1',
                'sth2',
                'sth3'
            ],
        },
        {
            type: 'static',
            header: 'Static Content Slide 2',
            content: '<p>Cum sociis natoque penatibus et magnis dis</p>',
        },
        {
            type: 'outro',
            header: 'Outro Slide',
            content: outroContent,
        },
    ],
};



