<template>
<div class="slide slide-quiz" :class="conf.class ? conf.class : ''" ref="container">

    <h1>{{ conf.header }}</h1>
    <div class="question">{{ conf.question }}</div>
    <ul class="answers">
        <li v-bind:key="index" v-for="(answer, index) in conf.answers">
            <input type="radio"
                name="quiz"
                :id="'answer'+index"
                :checked="previousAnswer.answer === index ? true : false"
                @change="checkAnswer(index, $event)" />
            <label :for="'answer'+index" v-html="answer" />
        </li>
    </ul>
</div>
</template>

<script>
export default {
    props: {
        conf: Object,
        saveAnswer: Function,
        previousAnswer: Object,
        popup: Function,
    },
    methods: {
        checkAnswer(value, e) {
            e.target.checked = true;

            if (value === this.conf.answer) {
                this.saveAnswer({
                    answer: value,
                    isCorrect: true,
                });
                this.popup(true, this.conf.correctMessage);
            } else {
                this.saveAnswer({
                    answer: value,
                    isCorrect: false,
                });
                this.popup(false, this.conf.incorrectMessage);
            }
        },
    },
};
</script>



