<template>
<div id="app">
    <!-- Slide content -->
    <div ref="transitiondiv" class="transition">
        <component :is="slideComponent" :conf="content[slideNumber]" :saveAnswer="saveAnswer" :previousAnswer="answers[slideNumber]" :popup="popup" />
    </div>

    <!-- Answer validation popup -->
    <div class="popup-container" v-if="showPopup">
        <div class="popup-dialog">
            <div class="content" v-html="popupContent" />
            <el-button v-if="showPopupButton" @click="hidePopup">Try again</el-button>
        </div>
    </div>

    <!-- Slider navigation -->
    <nav>
        <el-button @click="gotoPrevious" v-if="slideNumber !== 0 && slideNumber !== content.length - 1" class="prev-button">Previous</el-button>
        <el-button @click="gotoNext" v-if="slideNumber !== content.length - 1" class="next-button">Next</el-button>
        <el-button @click="restart" v-if="slideNumber === content.length - 1" class="next-button">Restart</el-button>
    </nav>
</div>
</template>

<script>
import BooleanSlide from './components/BooleanSlide.vue';
import QuizSlide from './components/QuizSlide.vue';
import MultiSlide from './components/MultiSlide.vue';
import StaticSlide from './components/StaticSlide.vue';
import DragAndDropSlide from './components/DragAndDropSlide.vue';
import SliderSlide from './components/SliderSlide.vue';
import config from './data.js';

export default {
    name: 'app',
    data() {
        return this.getDefaultData();
    },
    methods: {
        gotoPrevious() {
            const nextSlide = this.slideNumber - 1;
            this.initSlideChange(nextSlide);
        },
        gotoNext() {
            const nextSlide = this.slideNumber + 1;
            this.initSlideChange(nextSlide);
        },
        initSlideChange(nextSlide) {
            this.showPopup = false;
            this.$refs.transitiondiv.style.opacity = 0;

            setTimeout(() => {
                // Check what is the type of next slide and choose proper component
                switch (this.content[nextSlide].type) {
                    case 'boolean': {
                        this.slideComponent = BooleanSlide;
                        break;
                    }
                    case 'drag-n-drop': {
                        this.slideComponent = DragAndDropSlide;
                        break;
                    }
                    case 'quiz': {
                        this.slideComponent = QuizSlide;
                        break;
                    }
                    case 'multi': {
                        this.slideComponent = MultiSlide;
                        break;
                    }
                    case 'slider': {
                        this.slideComponent = SliderSlide;
                        break;
                    }
                    default: {
                        this.slideComponent = StaticSlide;
                        break;
                    }
                }
                this.slideNumber = nextSlide;
                this.$refs.transitiondiv.style.opacity = 1;
                if (!this.answers[nextSlide]) {
                    this.answers[nextSlide] = {};
                } else if (this.answers[nextSlide].isCorrect !== undefined) {
                    if (this.answers[nextSlide].isCorrect) {
                        this.showPopup = true;
                        this.popupContent = this.content[nextSlide].correctMessage;
                    } else {
                        this.showPopup = true;
                        this.showPopupButton = true;
                        this.popupContent = this.content[nextSlide].incorrectMessage;
                    }
                } else {
                    this.showPopup = false;
                }
            }, 500);
        },
        restart() {
            Object.assign(this.$data, this.getDefaultData());
        },
        saveAnswer(answer, showPopup = true) {
            this.answers[this.slideNumber] = answer;
            this.showPopup = showPopup;
        },
        getDefaultData() {
            return {
                slideComponent: StaticSlide,
                slideNumber: 0,
                content: config.slides,

                answers: {},

                showPopup: false,
                showPopupButton: false,
                popupContent: ''
            };
        },
        popup(isCorrect, msg) {
            if (isCorrect) {
                this.popupContent = msg;
                this.showPopupButton = false; // try again button
            } else {
                this.popupContent = msg;
                this.showPopupButton = true; // try again button
            }

            this.showPopup = true;
        },
        hidePopup() {
            this.showPopup = false;
        }
    }
};
</script>

<style lang="scss" src="./styles/style.scss" />



